import React from 'react';
import Jumbotron from 'react-bootstrap/Jumbotron';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

function Hero(props){

    return(
        <Jumbotron className="bg-transparent p-0">
            <Container fluid={true}>
                <Row className="justify-content-center py-5">
                    <Col md={6} sm={12}>
                        {props.title && <h1 className="display-2 font-weight-bolder">{props.title}</h1>}
                        {props.subtitle && <h3 className="display-3 font-weight-light">{props.subtitle}</h3>}
                        {props.text && <h3 className="lead-1 font-weight-light">{props.text}</h3>}
                    </Col>
                </Row>
            </Container>
        </Jumbotron>
    );
}
export default Hero;